@import '../../styles/variables';

.Player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  
  .TimeControl {
    width: 50%;
    display: flex;
    align-items: center;

    input {
      width: 100%;
      -webkit-appearance: none;
      background: transparent;
      cursor: pointer;
    }
  
    p {
      color: $TextPrimary;
      padding: 1rem;
    }
  }

  .PlayControl {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
  
    width: 40%;

    svg {
      cursor: pointer;
      color: $accent;
    }
  }
  
  .Track {
    width: 100%;
    height: 1rem;
    position: relative;
    overflow: hidden;
    border-radius: 1rem;
  }

  .AnimateTrack {
    background: rgb(204, 204, 204);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    padding: 1rem;
    pointer-events: none;
  }
  
  input[type="range"]:focus {
    outline: none;
  }

  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
  }
  
  @media screen and (max-width: 768px) {
    .TimeControl {
      width: 90%;
    }

    .PlayControl {
      width: 80%;
    }
  }
